import { isvalidPhone, isvalidEmail } from '@/utils'

const onlineTimes = [
  { value: 1350, label: '22:30' },
  { value: 1230, label: '20:30' }
]

const shortcuts = [
  {
    text: '',
    onClick (picker) {
      picker.$emit('pick', new Date())
    }
  },
  {
    text: '',
    onClick (picker) {
      const date = new Date()
      date.setTime(date.getTime() + 3600 * 1000 * 24)
      picker.$emit('pick', date)
    }
  }
]

const onlineAppformRules = {
  name: [
    { required: true, message: '', trigger: ['blur', 'change'] },
    { min: 2, max: 5, message: '', trigger: ['blur', 'change'] }
  ],
  phone: [
    { required: true, message: '', trigger: ['blur', 'change'] },
    { validator (rule, value, callback) {
      if (isvalidPhone(value)) {
        callback()
      } else {
        callback(new Error('11'))
      }
    },
    trigger: ['blur', 'change'] }
  ],
  email: [
    { required: true, message: '', trigger: ['blur', 'change'] },
    { validator (rule, value, callback) {
      if (isvalidEmail(value)) {
        callback()
      } else {
        callback(new Error(''))
      }
    },
    trigger: ['blur', 'change'] }
  ],
  date: [
    { type: 'number', required: true, message: '', trigger: ['blur', 'change'] }
  ],
  time: [
    { type: 'number', required: true, message: '', trigger: ['blur', 'change'] }
  ]
}

const onlineNeedTypes = [
  { value: 0, label: '' },
  { value: 1, label: '' },
  { value: 2, label: 'BUG' }
]

export { onlineTimes, shortcuts, onlineAppformRules, onlineNeedTypes }



